<script>
import base from "@/utils/base";

export default {
  name: 'MainPanel',
  mixins: [base],
  props: {
  },
  components: {
  },
  computed: {
    menus() {
      return [
        {
          text: this.$t('header.menus.home'),
          value: 'home'
        },
        {
          text: this.$t('header.menus.governance'),
          value: 'governance'
        },
        {
          text: this.$t('header.menus.docs'),
          value: 'docs'
        },
        {
          text: this.$t('header.menus.developers'),
          value: 'developers'
        },
        {
          text: this.$t('header.app'),
          value: 'app'
        },
        {
          text: 'English/中文',
          value: 'language'
        },
      ]
    },
  },
  data() {
    return {
      showMenusPopover: false,
      defaultActive: ''
    }
  },
  created() {
    window.addEventListener('click', e => {
      if (this.$refs['header-menus-btn']) {
        if (this.$refs['header-menus-btn'].contains(e.target)) {
          return
        }
      }

      if (this.$refs['header-menus']) {
        if (!this.$refs['header-menus'].contains(e.target)) {
          this.showMenusPopover = false
          return
        }
      }
    });
  },
  watch: {
    '$route.name'() {
      this.defaultActive = this.$route.name
      this.setTitle()
    }
  },
  methods: {
    onMenuSelected(val) {
      if (val.value === 'language') {
        this.onLangClick()
      } else if (val.value === 'home') {
        this.goHome()
      } else if (val.value === 'governance') {
        this.goGovernance()
      } else if (val.value === 'docs') {
        this.goDocs()
      } else if (val.value === 'developers') {
        this.goDevelopers()
      } else if (val.value === 'app') {
        this.goApp()
      }
      if (val.value !== 'language') {
        this.showMenusPopover = false
      }
    },
    goDocs() {
      let url = 'https://zh.docs.dc2c.finance/'
      if (this.$i18n.locale === 'en') {
        url = 'https://en.docs.dc2c.finance/'
      }
      window.open(url, '_blank')
    },
    goDevelopers() {
      window.open('https://github.com/dc2c', '_blank')
    },
    goHome() {
      if (this.$route.path !== '/') {
        this.$router.push({
          path: '/'
        })
      }
    },
    goGovernance() {
      if (this.$route.path !== '/governance') {
        this.$router.push({
          path: '/governance'
        })
      }
    },
    onLangClick() {
      let locale = this.$i18n.locale
      if (locale === 'zh') {
        locale = 'en'
      } else {
        locale = 'zh'
      }
      this.$i18n.locale = locale
      localStorage.setItem('locale', locale)
      this.setTitle()
    },
    setTitle() {
      document.title = `${this.$t('header.title.' + this.$route.meta.title)} | DC2C`
    }
  }
}
</script>

