</template>

<script>
import MainPanel from './views/main_panel.vue'

export default {
  name: 'App',
  components: {
    MainPanel
  },
  created() {
    const screenWidth = document.body.clientWidth
    this.$store.commit('setScreenWidth', screenWidth)
    window.onresize = () => {
      const screenWidth = document.body.clientWidth
      this.$store.commit('setScreenWidth', screenWidth)
    }
    this.init()
  },
  methods: {
    async init() {
    }
  }
}
</script>

