
export default {
    computed: {
        screenWidth() {
            return this.$store.state.screenWidth
        },
        isMobile() {
            return this.$store.state.screenWidth < 960
        },
        currentRouteName() {
            return this.$store.state.currentRouteName
        },
    },
    methods: {
        goApp() {
            window.open('https://app.dc2c.finance', '_blank')
        },
        goTwitter() {
            window.open('https://twitter.com/dc2cfinance', '_blank')
        },
        goGithub() {
            window.open('https://github.com/dc2c', '_blank')
        },
        goTelegram() {
            let url = 'https://t.me/DC2Csupport'
            if (this.$i18n.locale === 'en') {
                url = 'https://t.me/enDC2Csupport'
            }
            window.open(url, '_blank')
        },
        goDiscord() {
            window.open('https://discord.gg/ZsbHKr7NJG', '_blank')
        },
    }
}
